@import url('https://fonts.googleapis.com/css?family=Roboto:300');
@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');
body {
    background-color: rgb(229, 236, 229);
    color: #555;
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}


/* Presentation */

.presentation {
    text-align: center;
    position: absolute;
    /* background:linear-gradient(141deg, #ccc 25%, #eee 40%, #ddd 55%); */
    /* padding-top:40vh; */
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    -webkit-perspective: 1000;
    -webkit-transform: translate3d(0, 0, 0);
}

.namePresentation {
    margin-top: 30vh;
    animation: showup 7s;
    color: #99B898;
    font-size: 5.0em;
    font-weight: 12.5rem;
    display: inline-block;
    overflow: hidden;
}

.frontendPresentation {
    color: red;
    font-size: 2.5em;
    width: 0px;
    animation: reveal 7s;
}

.frontendPresentation span {
    animation: slidein 7s;
}

.builtTogether {
    color: #555;
    animation: showup 30s;
}

@keyframes showup {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes slidein {
    0% {
        margin-left: -50rem;
    }
    20% {
        margin-left: -50rem;
    }
    35% {
        margin-left: -50px;
    }
    100% {
        margin-left: -50px;
    }
}

@keyframes reveal {
    0% {
        opacity: 0;
        width: 0px;
    }
    20% {
        opacity: 1;
        width: 0px;
    }
    30% {
        width: 105%;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        width: 105%;
    }
}

@media screen and (max-width: 425px) {
    .presentation {
        width: 100%;
        height: 100%;
        -webkit-backface-visibility: hidden;
        -webkit-perspective: 1000;
        -webkit-transform: translate3d(0, 0, 0);
    }
    .namePresentation {
        animation: showup 7s;
        font-size: 2.1em;
        display: inline-block;
        overflow: hidden;
        margin-left: 0;
    }
    .frontendPresentation {
        color: red;
        font-size: 1.2em;
        width: 0px;
        animation: reveal 7s;
    }
    .frontendPresentation span {
        animation: slidein 7s;
    }
    .builtTogether {
        animation: showup 30s;
        font-size: 1.2em;
    }
    @keyframes showup {
        0% {
            opacity: 0;
        }
        20% {
            opacity: 1;
        }
        80% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
    @keyframes slidein {
        0% {
            margin-left: -50rem;
        }
        20% {
            margin-left: -50rem;
        }
        35% {
            margin-left: 0%;
        }
        100% {
            margin-left: 0%;
        }
    }
    @keyframes reveal {
        0% {
            opacity: 0;
            width: 0px;
        }
        20% {
            opacity: 1;
            width: 0px;
        }
        30% {
            width: 100%;
        }
        80% {
            opacity: 1;
        }
        100% {
            opacity: 0;
            width: 100%;
        }
    }
}


/* End Presentation page*/


