.fadeOut {
    opacity: 0;
    width: 0;
    height: 0;
    -webkit-transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
    transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}

.fadeIn {
    opacity: 1;
    width: 100px;
    height: 100px;
    -webkit-transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
    transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}
